.signUpScreen {
  max-width: 320px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.85);
}

.signUpScreen form {
  display: grid;
  flex-direction: column;
}

.signUpScreen form input {
  outline-width: 0;
  height: 40px;
  margin-bottom: 14px;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
  font-size: 1.3rem;
}

.signUpScreen form button {
  padding: 16px 20px;
  font-size: 1.3rem;
  color: #fff;
  border-radius: 5px;
  background-color: #e50914;
  font-weight: bold;
  cursor: pointer;
  border: none;
  margin-top: 20px;
}

.signUpScreen form h4 {
  text-align: left;
  margin-top: 30px;
  font-size: 1.3rem;
}

.signUpScreen-gray {
  color: gray;
  font-size: inherit;
}

.signUpScreen-link {
  font-size: inherit;
  cursor: pointer;
}

.signUpScreen-link:hover {
  text-decoration: underline;
}
